import { formatNumberWithKMB } from '@cetus/utils'

// è‡ªå®šä¹‰ X è½´åˆ»åº¦ç»„ä»¶
const CustomizedXAxisTick = ({ x, y, payload, index, total, isShowYAxis }: any) => {
  const isLastTick = index === total - 1
  return (
    <g transform={`translate(${x},${y})`}>
      <text x={0} y={0} dy={isShowYAxis ? 24 : 16} textAnchor={isLastTick ? 'end' : 'middle'} fill="#909CA4" fontSize={12} fontFamily="Inter">
        {payload.value}
      </text>
    </g>
  )
}

const CustomizedYAxisTick = ({ x, y, payload }: any) => {
  return (
    <g transform={`translate(0,${y})`}>
      <text x={0} y={0} dy={16} textAnchor="start" fill="#909CA4" fontSize={12} fontFamily="Inter">
        ${formatNumberWithKMB(payload.value)}
      </text>
    </g>
  )
}

